:host .checkbox {
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font: var(--mdx-comp-checkbox-font-label);

  &__wrapper {
    position: relative;
    display: flex;
    align-items: center;
    height: var(--mdx-comp-checkbox-font-label-line-height);
  }

  &__label-text {
    padding-left: var(--mdx-comp-checkbox-gap-box-label);
  }

  &__input {
    cursor: pointer;
    border-style: solid;
    border-radius: var(--mdx-comp-checkbox-border-radius-box);
    border-width: var(--mdx-comp-checkbox-border-width-default);
    border-color: var(--mdx-comp-checkbox-unchecked-input-border-default);
    min-height: var(--mdx-comp-checkbox-sizing-default);
    min-width: var(--mdx-comp-checkbox-sizing-default);
    margin: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    background-color: var(--mdx-comp-checkbox-unchecked-input-background-default);

    &:hover {
      background-color: var(--mdx-comp-checkbox-unchecked-input-background-hover);
      border-color: var(--mdx-comp-checkbox-unchecked-input-border-hover);
    }

    &:focus-visible {
      outline-style: solid;
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
    }
  }

  &__icon {
    opacity: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    color: var(--mdx-comp-checkbox-checked-input-icon-border-color-default);
    min-height: var(--mdx-comp-checkbox-sizing-default);
    min-width: var(--mdx-comp-checkbox-sizing-default);
    transition: var(--mdx-comp-checkbox-transition-fade-icon-in-out);

    &:hover {
      color: var(--mdx-comp-checkbox-checked-input-icon-border-color-hover);
    }
  }

  &__description {
    font: var(--mdx-comp-checkbox-font-description);
    color: var(--mdx-comp-checkbox-unchecked-description-color-default);
    margin-top: var(--mdx-comp-checkbox-gap-label-description);
    padding-left: calc(var(--mdx-comp-checkbox-sizing-default) + var(--mdx-comp-checkbox-gap-box-label));

    &:hover {
      color: var(--mdx-comp-checkbox-unchecked-description-color-hover);
    }
  }

  &__label {
    transition: var(--mdx-comp-checkbox-transition-fade-icon-in-out);
  }

  &__indicator {
    margin-left: var(--mdx-comp-checkbox-gap-indicator-label);
    color: var(--mdx-comp-checkbox-unchecked-indicator-color-default);
    font: var(--mdx-comp-checkbox-font-indicator);

    &:hover {
      color: var(--mdx-comp-checkbox-checked-indicator-color-hover);
    }
  }
}

:host([variant='border']) .checkbox {
  &__label {
    cursor: pointer;
    border-style: solid;
    border-width: var(--mdx-comp-checkbox-border-width-default);
    border-color: var(--mdx-comp-checkbox-unchecked-border-color-default);
    border-radius: var(--mdx-comp-checkbox-border-radius-default);
    font: var(--mdx-comp-checkbox-font-label);
    color: var(--mdx-comp-checkbox-unchecked-label-color-default);
    padding: var(--mdx-comp-checkbox-padding-vertical-default) var(--mdx-comp-checkbox-padding-horizontal-default);

    &:hover {
      background-color: var(--mdx-comp-checkbox-unchecked-background-color-hover);
      border-color: var(--mdx-comp-checkbox-unchecked-border-color-hover);
      border-width: var(--mdx-comp-checkbox-border-width-hover);
    }
  }
}

:host([variant='no-border']) .checkbox {
  &__label {
    cursor: pointer;
    background-color: var(--mdx-comp-checkbox-unchecked-background-color-default);
    color: var(--mdx-comp-checkbox-unchecked-label-color-default);
    padding: var(--mdx-comp-checkbox-padding-vertical-default) var(--mdx-comp-checkbox-padding-horizontal-default)
      var(--mdx-comp-checkbox-padding-vertical-default) 0;

    &:hover {
      background-color: var(--mdx-comp-checkbox-unchecked-background-color-hover);
      border-radius: var(--mdx-comp-checkbox-border-radius-default);
    }
  }
}

:host([checked]) .checkbox {
  &__label {
    border-color: var(--mdx-comp-checkbox-checked-border-color-default);
    background-color: var(--mdx-comp-checkbox-checked-background-color-default);
    color: var(--mdx-comp-checkbox-checked-label-color-default);

    &:hover {
      border-color: var(--mdx-comp-checkbox-checked-border-color-hover);
      background-color: var(--mdx-comp-checkbox-checked-background-color-hover);
      border-width: var(--mdx-comp-checkbox-border-width-hover);
    }
  }

  &__input {
    border-style: solid;
    border-color: var(--mdx-comp-checkbox-checked-border-color-default);
    border-width: var(--mdx-comp-checkbox-border-width-default);
  }

  &__description {
    color: var(--mdx-comp-checkbox-checked-description-color-default);

    &:hover {
      color: var(--mdx-comp-checkbox-checked-description-color-hover);
    }
  }

  &__icon {
    opacity: 1;
  }

  &__indicator {
    color: var(--mdx-comp-checkbox-checked-indicator-color-default);

    &:hover {
      color: var(--mdx-comp-checkbox-checked-indicator-color-hover);
    }
  }
}

:host([disabled]) .checkbox {
  &__label {
    background-color: var(--mdx-comp-checkbox-unchecked-background-color-disabled);
    border-color: var(--mdx-comp-checkbox-unchecked-border-color-disabled);
    border-width: var(--mdx-comp-checkbox-border-width-disabled);
    color: var(--mdx-comp-checkbox-unchecked-label-color-disabled);

    &:hover {
      background-color: var(--mdx-comp-checkbox-unchecked-background-color-disabled);
      border-color: var(--mdx-comp-checkbox-unchecked-border-color-disabled);
      color: var(--mdx-comp-checkbox-unchecked-label-color-disabled);
    }
  }

  &__input {
    background-color: var(--mdx-comp-checkbox-unchecked-input-background-disabled);
    border-color: var(--mdx-comp-checkbox-unchecked-input-border-disabled);
    border-width: var(--mdx-comp-checkbox-border-width-disabled);
  }

  &__description {
    color: var(--mdx-comp-checkbox-checked-description-color-disabled);
    background-color: var(--mdx-comp-checkbox-unchecked-background-color-disabled);

    &:hover {
      color: var(--mdx-comp-checkbox-checked-description-color-disabled);
      background-color: var(--mdx-comp-checkbox-unchecked-background-color-disabled);
    }
  }
  &__indicator {
    color: var(--mdx-comp-checkbox-unchecked-indicator-color-disabled);
  }
}

:host([disabled][checked]) .checkbox {
  &__label {
    border-color: var(--mdx-comp-checkbox-checked-border-color-disabled);
    border-width: var(--mdx-comp-checkbox-border-width-disabled);
    background-color: var(--mdx-comp-checkbox-checked-background-color-disabled);

    &:hover {
      background-color: var(--mdx-comp-checkbox-checked-background-color-default);
      border-color: var(--mdx-comp-checkbox-checked-border-color-disabled);
      border-width: var(--mdx-comp-checkbox-border-width-disabled);
    }
  }

  &__input {
    border-color: var(--mdx-comp-checkbox-checked-input-border-disabled);
    background-color: var(--mdx-comp-checkbox-checked-input-background-disabled);
    border-width: var(--mdx-comp-checkbox-border-width-disabled);
  }

  &__description {
    color: var(--mdx-comp-checkbox-checked-description-color-disabled);
    background-color: var(--mdx-comp-checkbox-checked-background-color-disabled);
  }

  &__icon {
    color: var(--mdx-comp-checkbox-checked-input-icon-border-color-disabled);
  }

  &__indicator {
    color: var(--mdx-comp-checkbox-checked-indicator-color-disabled);
  }
}

:host([error]) .checkbox {
  &__error-message {
    margin-top: var(--mdx-comp-checkbox-gap-container-error);
  }
}
