import { Component, Element, Event, EventEmitter, Host, Listen, Prop, State, h } from '@stencil/core';
import { BADGE_SIZES, BADGE_VARIANTS } from '../../badge/exports';
import { BUTTON_TYPE, BUTTON_VARIANTS } from '../../button/exports';
import { ICON_SIZES } from '../../icons/exports';
import { LINK_AS, LINK_VARIANTS } from '../../link/exports';

/**
 * @slot categories - a list of filter categories (mdx-filter-category-item)
 */

@Component({
  tag: 'mdx-filter-category',
  styleUrl: 'filter-category.scss',
  shadow: true,
})
export class FilterCat {
  @Prop({ reflect: true })
  open = false;

  @Prop({ reflect: true })
  view: 'category' | 'filters' = 'category';

  @Prop()
  buttonLabel: string;

  @Prop()
  closeButtonAriaLabel: string;

  @Prop()
  headerTitle: string;

  @Prop()
  submitButtonText: string;

  @Prop()
  backButtonText: string;

  @Prop()
  resetLinkText: string;

  @Prop()
  resetAllLinkText: string;

  @Prop()
  selectAllLinkText: string;

  @Prop()
  selectedItems: Record<string, { label: string; value: string }[]> = {};

  @State()
  selectedCategoryId?: string;

  @State()
  isMobile = window.innerWidth <= 768;

  @Element()
  hostElement: HTMLMdxFilterCategoryElement;

  @Event()
  filterCategoryChanged: EventEmitter<typeof this.selectedItems>;

  @Event()
  filterSubmit: EventEmitter<typeof this.selectedItems>;

  @Listen('click', { target: 'window' })
  handleOutsideClick(event: MouseEvent) {
    const path = event.composedPath();

    if (!path.includes(this.hostElement)) {
      this.open = false;

      if (this.selectedCategoryId !== undefined) {
        this.selectedCategoryId = undefined;
        this.view = 'category';
      }
    }
  }

  @Listen('toggleCheckbox')
  toggleCheckboxHandler({ detail }: CustomEvent<{ checked: boolean; value: string; label: string }>) {
    if (this.selectedCategoryId === undefined) {
      return;
    }
    if (!detail.checked) {
      this.selectedItems = {
        ...this.selectedItems,
        [this.selectedCategoryId]: this.selectedItems[this.selectedCategoryId]?.filter(
          (item) => item.value !== detail.value,
        ),
      };
    } else if (detail.checked && !this.selectedItems[this.selectedCategoryId]?.find((item) => item.value === detail.value)) {
      this.selectedItems = {
        ...this.selectedItems,
        [this.selectedCategoryId]: [
          ...(this.selectedItems[this.selectedCategoryId] ?? []),
          { label: detail.label, value: detail.value ?? '' },
        ],
      };
    }
    this.filterCategoryChanged.emit(this.selectedItems);
  }

  @Listen('filterFlyoutClose')
  filterFlyoutCloseHandler() {
    this.open = false;
  }

  @Listen('categoryClick')
  categoryClickHandler({ detail }: CustomEvent<{ categoryId: string }>) {
    this.view = 'filters';
    this.selectedCategoryId = detail.categoryId;

    const categoryFilters = this.selectedCategoryItem?.childNodes;
    if (categoryFilters) {
      this.hostFiltersPlaceholder?.replaceChildren();
      categoryFilters.forEach((filter) => {
        this.hostFiltersPlaceholder?.append(filter.cloneNode(true));
      });
    }
  }

  @Listen('resize', { capture: true, target: 'window' })
  onWindowResize() {
    this.isMobile = window.innerWidth <= 768;
  }

  private get hostFiltersPlaceholder() {
    return this.hostElement.shadowRoot?.querySelector('#filter_placeholder');
  }

  private clearFilterHandler(categoryId?: string) {
    if (categoryId !== undefined) {
      this.selectedItems = { ...this.selectedItems, [categoryId]: [] };
    } else {
      this.selectedItems = {};
    }
    this.hostFiltersPlaceholder?.querySelectorAll('mdx-checkbox').forEach((checkbox) => {
      checkbox.checked = false;
    });
    this.filterCategoryChanged.emit(this.selectedItems);
  }

  private selectAll() {
    this.hostFiltersPlaceholder?.querySelectorAll('mdx-checkbox').forEach((checkbox) => {
      checkbox.checked = true;
      if (this.selectedCategoryId === undefined) {
        return;
      }
      if (!this.selectedItems[this.selectedCategoryId]?.find((item) => item.value === checkbox.value)) {
        this.selectedItems = {
          ...this.selectedItems,
          [this.selectedCategoryId]: [
            ...(this.selectedItems[this.selectedCategoryId] ?? []),
            { label: checkbox.label, value: checkbox.value ?? '' },
          ],
        };
      }
    });
    this.filterCategoryChanged.emit(this.selectedItems);
  }

  componentDidRender() {
    if (this.selectedCategoryId !== undefined) {
      this.hostFiltersPlaceholder?.querySelectorAll('mdx-checkbox').forEach((checkbox) => {
        checkbox.checked =
          this.selectedCategoryId != undefined &&
          !!this.selectedItems[this.selectedCategoryId]?.find((item) => item.value === checkbox.value);
      });
    } else {
      this.hostElement?.querySelectorAll('mdx-filter-category-item').forEach((category) => {
        const categoryId = category.getAttribute('category-id');
        if (!categoryId) {
          return;
        }
        const numberOfSelectedFilters = this.getSelectedFiltersCount(categoryId);
        if (numberOfSelectedFilters === 0) {
          category.removeAttribute('badge');

          return;
        }
        category.setAttribute('badge', numberOfSelectedFilters.toString());
      });
    }
  }

  private get selectedCategoryItem() {
    return this.selectedCategoryId !== undefined
      ? Array.from(this.hostElement?.querySelectorAll('mdx-filter-category-item')).find(
          (item) => item.getAttribute('category-id') === this.selectedCategoryId,
        )
      : undefined;
  }

  private getSelectedFiltersCount(categoryId?: string) {
    return categoryId !== undefined
      ? this.selectedItems[categoryId]?.length ?? 0
      : Object.values(this.selectedItems).reduce((acc, curr) => acc + curr.length, 0);
  }

  render() {
    const selectedFilterCount = this.getSelectedFiltersCount(this.selectedCategoryId);

    return (
      <Host>
        {this.isMobile ? (
          <mdx-icon-button
            class="filter-category__toggle-button"
            onClick={() => (this.open = !this.open)}
            aria-expanded={this.open ? 'true' : 'false'}
            ariaLabel={this.buttonLabel}
          >
            <mdx-icon-sliders size={ICON_SIZES.SM} />
          </mdx-icon-button>
        ) : (
          <mdx-button
            class="filter-category__toggle-button"
            onClick={() => (this.open = !this.open)}
            aria-expanded={this.open ? 'true' : 'false'}
          >
            <mdx-icon-sliders size={ICON_SIZES.SM} /> {this.buttonLabel}
          </mdx-button>
        )}
        <mdx-filter-flyout open={this.open} role="dialog" aria-label={this.headerTitle}>
          <mdx-filter-flyout-header closeButtonAriaLabel={this.closeButtonAriaLabel}>
            {this.selectedCategoryId === undefined ? (
              <h2 slot="header">{this.headerTitle}</h2>
            ) : (
              <h2 slot="header">{this.selectedCategoryItem?.getAttribute('name')}</h2>
            )}
            {selectedFilterCount > 0 ? (
              <mdx-badge slot="header" size={BADGE_SIZES.MD} variant={BADGE_VARIANTS.SECONDARY}>
                {selectedFilterCount}
              </mdx-badge>
            ) : null}
          </mdx-filter-flyout-header>

          <mdx-filter-flyout-list class="categories">
            <mdx-link
              slot="controls"
              variant={LINK_VARIANTS.STANDALONE}
              as={LINK_AS.BUTTON}
              type={BUTTON_TYPE.BUTTON}
              onClick={() => this.clearFilterHandler()}
            >
              {this.resetAllLinkText}
            </mdx-link>
            <slot name="categories" slot="items" />
          </mdx-filter-flyout-list>
          <mdx-filter-flyout-list class="filters">
            <mdx-link
              slot="controls"
              variant={LINK_VARIANTS.STANDALONE}
              as={LINK_AS.BUTTON}
              type={BUTTON_TYPE.BUTTON}
              onClick={() => this.selectAll()}
            >
              {this.selectAllLinkText}
            </mdx-link>

            <svg
              class="filter-category__dot-spacer"
              width="2"
              height="2"
              slot="controls"
              viewBox="0 0 2 2"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <circle cx="1" cy="1" r="1" fill="currentColor" />
            </svg>

            <mdx-link
              slot="controls"
              variant={LINK_VARIANTS.STANDALONE}
              as={LINK_AS.BUTTON}
              type={BUTTON_TYPE.BUTTON}
              onClick={() => this.clearFilterHandler(this.selectedCategoryId)}
            >
              {this.resetLinkText}
            </mdx-link>
            <div id="filter_placeholder" slot="items"></div>
          </mdx-filter-flyout-list>
          <mdx-filter-flyout-footer>
            <mdx-button
              full-width
              slot="button"
              type={BUTTON_TYPE.SUBMIT}
              onClick={() => {
                this.filterSubmit.emit(this.selectedItems);
                this.open = false;
                this.selectedCategoryId = undefined;
                this.view = 'category';
              }}
            >
              {this.submitButtonText}
            </mdx-button>
            {this.selectedCategoryId != undefined && (
              <mdx-button
                full-width
                slot="button"
                variant={BUTTON_VARIANTS.TERTIARY}
                onClick={() => {
                  this.selectedCategoryId = undefined;
                  this.view = 'category';
                }}
              >
                {this.backButtonText}
              </mdx-button>
            )}
          </mdx-filter-flyout-footer>
        </mdx-filter-flyout>
      </Host>
    );
  }
}
