:host {
  .filter-category {
    &__toggle-button {
      display: block;
      margin-bottom: var(--mdx-comp-select-gap-content-below);
    }

    &__dot-spacer {
      color: var(--mdx-comp-filter-flyout-elements-header-dot-spacer-color-default);
    }
  }

  mdx-filter-flyout {
    position: absolute;
    width: var(--mdx-comp-filter-flyout-sizing-width);
    max-height: 0;
    overflow: hidden;
    transition: var(--mdx-comp-flyout-transition-expand-collapse);
  }

  .filters,
  .categories {
    overflow: hidden auto;
  }
}

:host([view='category']) {
  .filters {
    display: none;
  }
}
:host([view='filters']) {
  .categories {
    display: none;
  }
}

:host([open]) mdx-filter-flyout {
  max-height: 100vh;
  box-shadow: var(--mdx-comp-flyout-box-shadow-default);
  transition: var(--mdx-comp-flyout-transition-expand-collapse);
}
