import { Component, Element, Event, EventEmitter, h, Prop } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { CHECKBOX_VARIANTS } from './export';

/**
 * @slot description - Slot for the description
 */
@Component({
  tag: 'mdx-checkbox',
  styleUrl: 'checkbox.scss',
  shadow: true,
})
export class Checkbox {
  @Element()
  host: HTMLMdxCheckboxElement;

  @Event()
  toggleCheckbox: EventEmitter<{ checked: boolean; value: string; label: string }>;

  @Prop({ reflect: true })
  variant: (typeof CHECKBOX_VARIANTS)[keyof typeof CHECKBOX_VARIANTS] = CHECKBOX_VARIANTS.WITH_BORDER;

  @Prop({ reflect: true })
  label!: string;

  @Prop()
  name?: string;

  @Prop()
  error?: string;

  @Prop({ reflect: true })
  checked = false;

  @Prop()
  disabled = false;

  @Prop()
  indicator?: string | number;

  @Prop({ reflect: true })
  value: string;

  render() {
    return (
      <div class="checkbox">
        <label class="checkbox__label">
          <div class="checkbox__wrapper">
            <input
              class="checkbox__input"
              type="checkbox"
              name={this.name}
              disabled={this.disabled}
              checked={this.checked}
              onChange={(event) => {
                this.checked = (event.target as HTMLInputElement).checked;
                this.toggleCheckbox.emit({ checked: this.checked, value: this.value, label: this.label });
              }}
              value={this.value}
            />
            <mdx-icon-check class="checkbox__icon" size={ICON_SIZES.XS} />
            <span class="checkbox__label-text">{this.label}</span>
            {this.indicator !== undefined ? <span class="checkbox__indicator">{`(${this.indicator})`}</span> : null}
          </div>
          {this.host.querySelector('[slot="description"]') && (
            <span class="checkbox__description">
              <slot name="description" />
            </span>
          )}
        </label>
        {this.error && <mdx-error-hint class="checkbox__error-message" text={this.error} />}
      </div>
    );
  }
}
